/*  Responsive of all device */
@media only screen and (max-width: 600px) {
  .main-contact {
    height: 100%;
  }
  h1 {
    font-size: 2rem;
  }
  .contact-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
